function weakEqual(a: any, b: any) {
    if (typeof a === "object") {
        if (typeof b === "object") {
            if (a !== b) {
                if (a === null || b === null) {
                    return [a, b];
                }
                let keys = new Set(Object.keys(a).concat(Object.keys(b)));
                for (const key of Array.from(keys)) {
                    if (key === "range" || key === "loc") {
                        continue;
                    }
                    let res = weakEqual(a[key], b[key]);
                    if (res !== true) {
                        res.unshift(a, b, key);
                        return res;
                    }
                }
            }
            return true;
        } else {
            return [a, b];
        }
    } else {
        return a === b || [a, b];
    }
}

function deepEqual(a: any, b: any) {
    if (typeof a === "object") {
        if (typeof b === "object") {
            if (a !== b) {
                if (a === null || b === null) {
                    return [a, b];
                }
                let keys = new Set(Object.keys(a).concat(Object.keys(b)));
                for (const key of Array.from(keys)) {
                    let res = deepEqual(a[key], b[key]);
                    if (res !== true) {
                        res.unshift(a, b, key);
                        return res;
                    }
                }
            }
            return true;
        } else {
            return [a, b];
        }
    } else {
        return a === b || [a, b];
    }
}


